import { useCallback, useState } from 'react'
import useSWR, { useSWRConfig } from 'swr'
import api from '../services/api'

const fetchPhases = async (url, filters) => {
    const fetchUrl = url + getFilteredUrl(filters)
    return api.get(fetchUrl).then(res => res.data)
}

const fetchPhaseProducts = async (url, filters) => {
    const fetchUrl = url + getFilteredProducts(filters)
    return api.get(fetchUrl).then(res => res.data)
}

function getFilteredUrl (filters) {
    let url = "?"
    url += filters.product !== "" ? `&product=${filters.product}` : ""
    url += filters.line !== "" ? `&line=${filters.line}` : ""
    url += filters.group !== "" ? `&group=${filters.group}` : ""
    url += filters.subgroup !== "" ? `&subgroup=${filters.subgroup}` : ""
    url += filters.collection !== "" ? `&collection=${filters.collection}` : ""
    url += filters.compound !== "" ? `&compound=${filters.compound}` : ""

    return url
}

function getFilteredProducts (filters) {
    let url = ""
    url += checkFilter("search", filters.reference)
    if (filters.tags.length !== 0) url += `&tags=${filters.tags?.map(tag => tag.id).join(',')}`;
    url += checkFilter("phase", filters.phase)
    url += checkFilter("typed", filters.typed)
    url += checkFilter("erp", filters.erp)
    url += checkFilter("generic", filters.generic)
    url += checkFilter("material", filters.material)
    url += checkFilter("store", filters.store)
    url += checkFilter("period", filters.period)
    url += checkFilter("line", filters.line)
    url += checkFilter("group", filters.group)
    url += checkFilter("subgroup", filters.subgroup)
    url += checkFilter("collection", filters.collection)
    url += checkFilter("stylist", filters.stylist)
    url += checkFilter("designer", filters.designer)
    url += checkFilter("compound", filters.compound)

    return url
}

function checkFilter (key, value) {
    if (value !== "" && value !== undefined && value !== null) {
        return `&${key}=${value}`
    }
    return ""
}

export default function usePhases () {
    const [filters, setFilters] = useState({
        reference: "",
        tags: "",
        line: "",
        group: "",
        subgroup: "",
        collection: "",
        material: "",
        stylist: "",
        designer: "",
        compound: "",
        noPhase: false,
        erp: null,
        generic: null
    })

    const { data, error, mutate } = useSWR(
        ['/phases/', filters], 
        fetchPhases,
        {
            revalidateIfStale: false,
            revalidateOnFocus: false,
        }
    )

    const filterPhases = useCallback((key, value) => {
        switch (key) {
            case "product":
                setFilters(filters => ({...filters, product: value}))
                break;
            case "line":
                setFilters(filters => ({...filters, line: value}))
                break;
            case "group":
                setFilters(filters => ({...filters, group: value}))
                if (value == "" || value == undefined || value == null) {
                    filterPhases("subgroup", "")
                }
                break;
            case "subgroup":
                setFilters(filters => ({...filters, subgroup: value}))
                break;
            case "collection":
                setFilters(filters => ({...filters, collection: value}))
                break;
            case "compound":
                setFilters(filters => ({...filters, compound: value}))
                break;
            default:
                break;
        }
    }, [])

    const removeProduct = useCallback((id) => {
        api.patch(`products/${id}/`, {phase: null}).then(() => {
            mutate()
        })
    }, [])

    const patchProduct = useCallback(async (product, phase) => {
        return api.patch(
            `products/${product.id}/`, 
            {phase: phase.id}
        ).then((res) => {
            mutate()
            return res
        }).catch((err) => err.response)
    })

    const addProduct = useCallback(async (product, phase) => {
        return api.patch(
            `products/${product.id}/`, 
            {phase: phase.id}
        ).then((res) => {
            mutate()
            return res
        }).catch((err) => err.response)
    })

    return {
        phases: data,
        phasesFilters: filters,
        isLoading: !error && !data,
        isError: error,
        updatePhases: mutate,
        filters,
        filterPhases,
        removeProduct,
        patchProduct,
        addProduct
    }
}

export function usePhaseFilters () {
    const [filters, setFilters] = useState({
        reference: "",
        tags: "",
        line: "",
        group: "",
        subgroup: "",
        collection: "",
        material: "",
        stylist: "",
        designer: "",
        compound: "",
        noPhase: false,
        erp: null,
        generic: null
    })

    const filter = useCallback((key, value) => {
        switch (key) {
            case "reference":
                setFilters(filters => ({...filters, reference: value}))
                break;
            case "tags":
                setFilters(filters => ({...filters, tags: value}))
                break;
            case "line":
                setFilters(filters => ({...filters, line: value}))
                break;
            case "group":
                setFilters(filters => ({...filters, group: value}))
                if (value == "" || value == undefined || value == null) {
                    filter("subgroup", "")
                }
                break;
            case "subgroup":
                setFilters(filters => ({...filters, subgroup: value}))
                break;
            case "collection":
                setFilters(filters => ({...filters, collection: value}))
                break;
            case "material":
                setFilters(filters => ({...filters, material: value}))
                break;
            case "erp":
                setFilters(filters => ({...filters, erp: value}))
                break;
            case "generic":
                setFilters(filters => ({...filters, generic: value}))
                break;
            case "stylist":
                setFilters(filters => ({...filters, stylist: value}))
                break;
            case "designer":
                setFilters(filters => ({...filters, designer: value}))
                break;
            default:
                break;
        }
    }, [])

    return {
        filters,
        filter
    }
}


export function usePhaseProducts (id) {
    const [filters, setFilters] = useState({
        reference: "",
        tags: "",
        line: "",
        group: "",
        subgroup: "",
        collection: "",
        material: "",
        stylist: "",
        designer: "",
        compound: "",
        noPhase: false,
        erp: null,
        generic: null
    })

    const { data, error, mutate } = useSWR(
        [`/products/?phase=${id}`, filters], 
        fetchPhaseProducts,
        {
            revalidateIfStale: false,
        }
    )
    const { mutate: generalMutate } = useSWRConfig()

    const filterPhase = useCallback((key, value) => {
        switch (key) {
            case "reference":
                setFilters(filters => ({...filters, reference: value}))
                break;
            case "tags":
                setFilters(filters => ({...filters, tags: value}))
                break;
            case "line":
                setFilters(filters => ({...filters, line: value}))
                break;
            case "group":
                setFilters(filters => ({...filters, group: value}))
                if (value == "" || value == undefined || value == null) {
                    filterPhase("subgroup", "")
                }
                break;
            case "subgroup":
                setFilters(filters => ({...filters, subgroup: value}))
                break;
            case "collection":
                setFilters(filters => ({...filters, collection: value}))
                break;
            case "material":
                setFilters(filters => ({...filters, material: value}))
                break;
            case "erp":
                setFilters(filters => ({...filters, erp: value}))
                break;
            case "generic":
                setFilters(filters => ({...filters, generic: value}))
                break;
            case "stylist":
                setFilters(filters => ({...filters, stylist: value}))
                break;
            case "designer":
                setFilters(filters => ({...filters, designer: value}))
                break;
            default:
                break;
        }
    }, [])

    const removeProduct = useCallback(async (id) => {
        return api.patch(`products/${id}/`, {phase: null}).then((res) => {
            mutate()
            return res
        })
    }, [])

    const patchProduct = useCallback(async (product, phase) => {
        return api.patch(
            `products/${product.id}/`, 
            {phase: phase.id}
        ).then((res) => {
            generalMutate([`/products/?phase=${phase.id}`, filters])
            mutate()
            return res
        })
    }, [])

    const addProduct = useCallback(async (product, phase) => {
        return api.patch(
            `products/${product.id}/`, 
            {phase: phase.id}
        ).then((res) => {
            mutate()
            return res
        }).catch((err) => err.response)
    }, [])

    return {
        products: data,
        isLoading: !error && !data,
        isError: error,
        updatePhase: mutate,
        filterPhase,
        setFilters,
        phaseOperations: {
            add: addProduct,
            remove: removeProduct,
            patch: patchProduct
        }
    }
}