import ViewProduct from "../../../components/Modals/Products/ViewProduct/ViewProduct"
import "./InlineComment.css"
import ModalBase from "../../../components/shared/ModalBase/ModalBase"
import ViewProductCompound from "../../../components/Modals/Compounds/ViewProductCompound/ViewProductCompound"
import { forwardRef } from "react"

export const InlineComment = forwardRef((props, ref) => {
    const comment = props.comment
    const date = new Date(comment.created_at)

    const SubjectCaller = () => {
        if (props.noCaller) return comment.subject

        return (
            <ModalBase
                caller={<>{comment.subject}</>}
                content={comment.type === "product" ? <ViewProduct productId={comment.subject_id} /> : <ViewProductCompound itemId={comment.subject_id} />}
                title={comment.subject}
            />
        )
    }

    return (
        <div className="comment-ct" ref={ref}>
            <div className="com-header">
                <p><b>{comment.user}</b> sobre <b><SubjectCaller /></b></p>
                <p>{date.toLocaleString("pt-BR")}</p>
            </div>
            <div className="com-body">
                <p>{comment.content}</p>
            </div>
        </div>
    )
})