import { createSlice } from "@reduxjs/toolkit";

const initialState = {
    id: null,
    username: null,
    unread_comments: null,
    access_token: null,
    refresh_token: null,
    preferencies: {
        itemColumns: 6
    }
}

export const userSlice = createSlice({
    name: "user",
    initialState,
    reducers: {
        login: (state, action) => {
            state.id = action.payload.id
            state.username = action.payload.username
            state.unread_comments = action.payload.unread_comments
            state.access_token = action.payload.access_token
            state.refresh_token = action.payload.refresh_token
        },
        logout: (state, action) => {
            state.id = null
            state.username = null
            state.unread_comments = null
            state.access_token = null
            state.refresh_token = null
        },
        updateTokens: (state, action) => {
            state.access_token = action.payload.access
            state.refresh_token = action.payload.refresh
        },
        updateUnreadComments: (state, action) => {
            state.unread_comments = action.payload.quantity
        },
        resetUnreadComments: (state, action) => {
            state.unread_comments = 0
        },
        changeItemColumns: (state, action) => {
            state.preferencies.itemColumns = action.payload.columns
        },
    }
})

export const { 
    login, logout, updateTokens, 
    resetUnreadComments, updateUnreadComments, changeItemColumns
} = userSlice.actions;

export const selectUser = (state) => state;
export default userSlice.reducer;