import { useState } from 'react';
import SideBar from '../../components/SideBar/SideBar'

import { TabView, TabPanel } from 'primereact/tabview';
import { Modal } from '@mui/material';

import './style.css'
import { ProductComments } from './Tabs/ProductComments/ProductComments';
import { ItemComments } from './Tabs/ItemComments/ItemComments';
import { AddCommentModal } from './AddCommentModal/AddCommentModal';
import { MentionedComments } from './Tabs/MentionedComments/MentionedComments';
import { createContext } from 'react';
import api from '../../services/api';
import { useInfiniteQuery, useQuery } from 'react-query';
import { useSelector } from 'react-redux';
import { selectUser } from '../../app/slices/userSlice';

export const FilteredComments = createContext()

export const useFilteredComments = (baseUrl, filters) => {
    let url = baseUrl
    let page_size = 15

    if (filters.user) {
        url += "&user=" + filters.user
    }

    if (filters.item_product) {
        url += "&item_product=" + filters.item_product
    }

    if (filters.item_period) {
        url += "&item_period=" + filters.item_period
    }

    if (filters.item_compound) {
        url += "&item_compound=" + filters.item_compound
    }

    url = url + `&limit=${page_size}&offset=`

    const fetchPages = ({ pageParam = 0 }) => api.get(url + pageParam).then(res => res.data)

    const {
        data,
        error,
        fetchNextPage,
        hasNextPage,
        isFetching,
        isFetchingNextPage,
        status,
    } = useInfiniteQuery(['comments', 'infinite', baseUrl, filters], fetchPages, {
        getNextPageParam: (lastPage, pages) => lastPage.next ? pages.length * page_size + 1 : undefined,
    })

    return {
        data,
        error,
        fetchNextPage,
        hasNextPage,
        isFetching,
        isFetchingNextPage,
        status,
    }
}

export const CommentsPage = () => {
    const user = useSelector(selectUser).user
    const [filters, setFilters] = useState({
        user: null,
        item_product: null,
        item_period: null,
        item_compound: null
    })
    const [activeIndex, setActiveIndex] = useState(0);
    const paraMim = `Para mim${user.unread_comments !== 0 && "*"}`
    const tabs = ["Produtos", "Itens", paraMim]
    const [open, setOpen] = useState(false)

    return (
        <div id="cm-pg">
            <SideBar commentsFilters={filters} filterComments={setFilters} tabName={tabs[activeIndex]} />
            <div className='content'>
                <FilteredComments.Provider value={filters}>
                    <TabView activeIndex={activeIndex} onTabChange={(e) => setActiveIndex(e.index)}>
                        <TabPanel header="Produtos">
                            <ProductComments />
                        </TabPanel>
                        <TabPanel header="Itens">
                            <ItemComments />
                        </TabPanel>
                        <TabPanel header={`Para mim${user.unread_comments !== 0 ? "*" : ""}`}>
                            <MentionedComments />
                        </TabPanel>
                    </TabView>
                </FilteredComments.Provider>
                <div className='add-com-btn' onClick={() => setOpen(true)}>
                    <i className="pi pi-plus" />
                </div>
            </div>
            <Modal open={open} onClose={() => setOpen(false)}>
                <>
                    <AddCommentModal closeModal={() => setOpen(false)}/>
                </>
            </Modal>
        </div>
    )
}