import React, { memo } from 'react';

import { Dropdown } from 'primereact/dropdown';
import useSWR from 'swr';
import api from '../../services/api';
import useProducts from '../../hooks/ProductHandlers';
import useCompounds from '../../hooks/CompoundHandlers';

function CommentsFilter(props) {
    const filter = props.filterComments
    const filters = props.commentsFilters

    const { data: users } = useSWR("users", () => api.get("users").then(res => res.data))
    const { products } = useProducts({paginated: false})
    const { compounds } = useCompounds()

    return (
        <>
            <Dropdown 
                value={filters.user}
                options={users} 
                optionLabel="username" optionValue="id"
                onChange={(e) => {
                    filter({...filters, user: e.value})
                }} 
                showClear={true}
                placeholder="Usuário"
            />
            {props.tabName === "Itens" &&
                <>
                    <Dropdown 
                        value={filters.item_product}
                        options={products} 
                        optionLabel="reference" optionValue="id"
                        onChange={(e) => {
                            filter({...filters, item_product: e.value})
                        }} 
                        showClear={true}
                        placeholder="Produto"
                    />
                    <Dropdown 
                        value={filters.item_compound}
                        options={compounds} 
                        optionLabel="name" optionValue="id"
                        onChange={(e) => {
                            filter({...filters, item_compound: e.value})
                        }} 
                        showClear={true}
                        placeholder="Composto"
                    />
                    {filters.item_compound &&
                        <Dropdown 
                            value={filters.item_period}
                            options={compounds.find(compound => compound.id === filters.item_compound).periods} 
                            optionLabel="name" optionValue="id"
                            onChange={(e) => {
                                filter({...filters, item_period: e.value})
                            }} 
                            showClear={true}
                            placeholder="Período"
                        />
                    }
                </>
            }
        </>
    )
}

export default memo(CommentsFilter)