import React, { useState } from "react";
import Modal from "@mui/material/Modal";
import { ButtonBase } from "@mui/material";
import { confirmDialog } from "primereact/confirmdialog";
import { Sidebar } from 'primereact/sidebar';
import api from "../../../services/api";

import "./ModalBase.css";
import useSWR from "swr";
import { InlineComment } from "../../../pages/CommentsPage/InlineComment/InlineComment";

export default function ModalBase(props) {
  const [open, setOpen] = useState(false);
  const [sideBarOpen, setSideBarOpen] = useState()
  const { data: comments } = useSWR(sideBarOpen ? props.commentsUrl : undefined, () => api.get(props.commentsUrl).then(res => res.data)) 
  const handleOpen = () => setOpen(true);
  let backdropClose = true;

  const changeBackgroupClose = (value) => {
    backdropClose = value;
  };

  const handleClose = (event, reason) => {
    if (reason === "backdropClick" && !backdropClose) {
      confirmClosing();
    } else {
      if (props.onClose) props.onClose();
      setOpen(false);
    }
  }

  const confirmClosing = () => {
    confirmDialog({
      message:
        "Tem certeza que deseja fechar o modal? Alterações não salvas serão perdidas",
      header: "Atenção",
      icon: "pi pi-exclamation-triangle",
      acceptLabel: "Sim",
      rejectLabel: "Não",
      accept: handleClose,
      reject: () => {},
    });
  };

  const Content = () => {
    return React.cloneElement(props.content, {
      closeModal: handleClose,
      backdropAutoClose: changeBackgroupClose,
    });
  };

  return (
    <>
      <ButtonBase
        style={{ display: "flex", flexDirection: "column" }}
        onClick={handleOpen}
        id={props.buttonId ? props.buttonId : ""}
      >
        {props.caller}
      </ButtonBase>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <div className="container">
          <div className="modal-header">
            <h3>{props.title}</h3>
            <div>
              {props.commentsIcon &&
                <>
                  <i className="pi pi-comments" onClick={() => setSideBarOpen(true)}/>
                  <Sidebar visible={sideBarOpen} onHide={() => setSideBarOpen(false)}>
                    {comments ?
                      comments.map(comment => <InlineComment comment={comment} key={comment.id} noCaller/>)
                      :
                      <i className="pi pi-spin pi-spinner" />
                    }
                  </Sidebar>
                </>
              }
              <ButtonBase onClick={handleClose} className="closeButton">
                X
              </ButtonBase>
            </div>
          </div>
          <div className="modal-content">
            <Content />
          </div>
        </div>
      </Modal>
    </>
  );
}
