import React, { memo, useEffect, useState } from 'react';
import './MobileSidebar.css'

import { useNavigate, useLocation } from "react-router-dom";

import { Sidebar } from 'primereact/sidebar';
import { Button } from 'primereact/button';

import ProductFilters from '../SideBarFilters/ProductFilters';
import CompoundsFilters from '../SideBarFilters/CompoundsFilters';
import PhasesFilter from '../SideBarFilters/PhasesFilter';

import DashboardIcon from '@mui/icons-material/Dashboard';
import FilterListIcon from '@mui/icons-material/FilterList';
import LogoutRoundedIcon from '@mui/icons-material/LogoutRounded';
import { ButtonBase } from '@mui/material';

function MobileSideBar(props) {
    const navigate = useNavigate()
    const location = useLocation()
    const [visible, setVisible] = useState(false)
    const [name, setName] = useState("")

    const Filters = () => {
        switch (location.pathname) {
            case "/products":
                return <ProductFilters 
                    productFilters={props.productFilters} 
                    filterProducts={props.filterProducts}
                />
            case "/phases":
                return <PhasesFilter 
                    phaseFilters={props.phaseFilters}
                    filterPhases={props.filterPhases}
                />
            case "/compounds":
                return <CompoundsFilters 
                    periodHooks={props.periodHooks}
                />
        }
    }

    const logout = () => {
        localStorage.removeItem("@greenMix:USERNAME")
        localStorage.removeItem("@greenMix:TOKENS")
        navigate('/', {replace: true})
    }
 
    useEffect(() => {
        if (!name) {
            let username = localStorage.getItem("@greenMix:USERNAME")
            setName(username)
        }
    }, [location])
    
    return (
        <>
        <Sidebar visible={visible} fullScreen onHide={() => setVisible(false)} className="dark">
            <div className={`sidebar-mob`}>
                <div className='identification'>
                    <h2>Olá, {name ? name : "User"}</h2>
                </div>
                <div className='dashboard'>
                    <div style={{display: "flex", gap: "0.25rem"}}>
                        <DashboardIcon />
                        <h4>Dashboard</h4>
                    </div>
                    <a onClick={() => navigate(`/products`)}>Produtos</a>
                    <a onClick={() => navigate(`/phases`)}>Fases</a>
                    <a onClick={() => navigate(`/compounds`)}>Compostos</a>
                </div>
                <div className='filters'>
                    <div style={{display: "flex", gap: "0.25rem"}}>
                        <FilterListIcon />
                        <h4>Filtros</h4>
                    </div>
                    <Filters />
                </div>
                <ButtonBase className='logout' onClick={logout}>
                    <LogoutRoundedIcon /> Logout
                </ButtonBase>
            </div>
        </Sidebar>

        <Button icon="pi pi-th-large" className='sidebar-mob--button' onClick={(e) => setVisible(true)}/>
        </>
    )
}

export default memo(MobileSideBar)