import React, { useState, useEffect } from "react";
import { Dropdown } from "primereact/dropdown";
import { ButtonBase } from "@mui/material";
import { InputNumber } from "primereact/inputnumber";
import { Calendar } from "primereact/calendar";
import "./AddDemand.css";
import useStores, { useDemands } from "../../../../hooks/DemandsHandlers";

export default function AddDemand(props) {
  const alreadyOpened = props.item.demands?.map((demand) => demand.store.id);
  const [details, setDetails] = useState({
    store: "",
    quantity: "",
    date: "",
  });
  const [dateView, setDateView] = useState();
  const [dateRestriction, setDateRestriction] = useState({
    start: null,
    end: null,
  });

  useEffect(() => {
    let deltaStart = props.period.start_date.split("-");
    let dateStart = new Date();
    dateStart.setDate(deltaStart[0]);
    dateStart.setMonth(deltaStart[1] - 1);
    dateStart.setFullYear(deltaStart[2]);

    setDateRestriction((d) => ({ ...d, start: dateStart }));

    let deltaEnd = props.period.end_date.split("-");
    let dateEnd = new Date();
    dateEnd.setDate(deltaEnd[0]);
    dateEnd.setMonth(deltaEnd[1] - 1);
    dateEnd.setFullYear(deltaEnd[2]);

    setDateRestriction((d) => ({ ...d, end: dateEnd }));
  }, []);

  useEffect(() => {
    let delta = details.date.split("-");
    let date = new Date();
    date.setDate(delta[0]);
    date.setMonth(delta[1] - 1);
    date.setFullYear(delta[2]);

    setDateView(date);
  }, [details.date]);

  const { stores } = useStores();
  const { addDemand } = useDemands(props.item.id);

  const handleSaving = () => {
    addDemand(props.item.id, details.store.id, details.quantity, details.date);
    props.close();
  };

  return (
    <div className="grid">
      <div className="addDemand">
        <div className="p-col">
          <Dropdown
            value={details.store}
            onChange={(e) => {
              setDetails({ ...details, store: e.value });
            }}
            options={stores.filter(
              (store) => !alreadyOpened.includes(store.id)
            )}
            optionLabel="name"
            placeholder="Selecione uma loja"
          />
        </div>
        <div className="p-col">
          <Calendar
            value={dateView}
            dateFormat="dd/mm/yy"
            minDate={dateRestriction.start}
            maxDate={dateRestriction.end}
            onChange={(e) => {
              setDetails((d) => ({
                ...d,
                date: `${e.value.getDate()}-${
                  e.value.getMonth() + 1
                }-${e.value.getFullYear()}`,
              }));
            }}
          />
        </div>
        <div className="p-col">
          <InputNumber
            value={details.quantity}
            locale="pt-BR"
            showButtons
            min={0}
            minFractionDigits={0}
            maxFracionDigits={0}
            onValueChange={(e) => {
              setDetails({
                ...details,
                quantity: e.value,
              });
            }}
          />
        </div>
      </div>
      <ButtonBase onClick={(e) => {
        handleSaving() 
        }} className="greenButton">
        Salvar
      </ButtonBase>
    </div>
  );
}
